
$accent: #d408e6;

body {
    color: #444;
    font-size: 16px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
        'Open Sans', 'Helvetica Neue', sans-serif;
    line-height: 1.65;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    margin-top: 0;
    font-weight: bold;
    color: #222;
}

p {
    margin-top: 0;
}

a {
    color: $accent;
    text-decoration: none;
}

a:hover {
    text-decoration: underline;
}

* {
    box-sizing: border-box;
}

.clickable {
    cursor: pointer;
    &:hover {
        color: $accent;
    }
}

.below-above {
    background-color: #efefef;

    > * {
        display: inline-block;
        padding: 0.5rem;
    }
}

code {
    font-family: SFMono-Regular, Menlo, Monaco, Consolas, 'Liberation Mono', 'Courier New',
        monospace;
    background: #efefef;
    padding: 0.25rem 0.25rem;
    border-radius: 0.25em;
}

.playground {
    max-width: 1200px;
    margin: 0 auto 5rem;

    &__header {
        padding: 1.5rem;
    }

    &__body {
        position: relative;

        display: flex;
        flex-flow: row nowrap;
        align-items: flex-start;
    }

    &__nav {
        position: sticky;
        top: 1rem;
        min-width: 240px;

        padding: 0 1.5rem;
    }

    &__content {
        display: flex;
        flex-flow: column;

        .example {
            display: grid;
            grid-template:
                'heading heading' auto
                'desc component' 1fr
                / 6fr 4fr;
            align-items: start;

            min-height: 200px;

            &:not(:last-child) {
                border-bottom: 1px solid #efefef;
            }
            padding-bottom: 2rem;
            margin-bottom: 2rem;

            h4 {
                grid-area: heading;
            }

            &__desc {
                grid-area: desc !important;
                padding-right: 1rem;
            }

            * {
                grid-area: component;
            }
        }
    }
}

@import '../assets/_layout.scss';
@import '../assets/_style.scss';
@import '../assets/_transitions.scss';
